.greetingForm {
}

.inputAndButtonContainer {
    display: flex;
    gap: 10px;
}

.error {
    margin-top: 7px;
    color: #C00;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.input {
    width: 372px;
    height: 36px;
    padding-top: 8px;
    padding-left: 12px;
    padding-bottom: 8px;
    color: #000;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.input:focus {

}

.errorInput {
    width: 372px;
    height: 36px;
    border: 1px solid #CC0000;
    border-radius: 5px;
}

.button {
    display: flex;
    width: 78px;
    height: 36px;
    border-radius: 5px;
    border: none;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    color: #FFF;
    background: #06C;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    opacity: 0.5;
}

.button::after {
    content: '';
    display: inline-block;
    position: absolute;
    bottom: -482px;
    background-color: #D9D9D9;
    width: 120vw;
    height: 1px;
}

.text {
    margin-top: 40px;
    margin-bottom: 10px;
    color: rgba(0, 0, 0, 0.50);
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.greeting {
    color: #000;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
